/* ----- 프로젝트 생성 상단 페이지 이동 경로 ----- */
/* --------------------------------------- */

.PMgmtRouter-item {
  height: 70%;
  color: #a7a9ad;
  display: flex;
  background-color: #242933;
  border-radius: 5px;
}

.PMgmtRouter-item .PMgmtRouter-select {
  margin: 0px 40px;
  font-size: 15px;
  color: #a7a9ad;
  height: 100%;

  display: flex;
  align-items: center;
  cursor: pointer;
}

.PMgmtRouter-item div {
  display: flex;
  align-items: center;
}

.PMgmtRouter-item .PMgmtRouter-select.active {
  color: white;
  position: relative;
}
.PMgmtRouter-item .PMgmtRouter-select.active div {
  position: absolute;

  bottom: 0px;

  width: 100%;
  height: 4px;
  background-color: #448fff;
  border-radius: 20px;
  border: none;
}
