
* {
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* 스크롤 바 숨기기 */
::-webkit-scrollbar {
  display: none !important;
}

body {
  background-color: #191c22;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
}

/* 메인 레이아웃 그리드 */
.container {
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 7vh 92vh 1vh;
    background-color: #191c22;
}

/* 메인 레이아웃 그리드 - 헤더 */
.header{
    grid-column: 2 / 3;
}

/* 메인 레이아웃 그리드 - 좌측 사이드바 */
.sidebar{
    grid-row: 1 / 4;
}