.sidebar_main
{
    text-align: center;
    width: 100%;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    font-size: 30px;
}

.sidebar_item .item{
    height:60px;
    width: 265px;
    color: #6c7890;
    cursor: pointer;

    border-radius: 0px 10px 10px 0px;
    border-bottom: 2px solid;
    border-top: 2px solid;
    border-right: 2px solid;
    border-color: #364053;

    display: flex;
    align-items: center;
    /* justify-content: center; */
    padding-left:30px;
}

.item_text{
    font-size: 17px;
    font-weight: 400;
    margin-left:25px;
}

#project_button_icon
{
   background: url("../images/icons/icon_3x/project_mgmt.png") no-repeat;
   background-size: 20px;
   background-position-y: center;
}

#data_button_icon
{
   background: url("../images/icons/icon_3x/data_mgmt@3x.png") no-repeat;
   background-size: 20px;
   background-position-y: center;
}

#logout_button_icon
{
   background: url("../images/icons/icon_3x/return@3x.png") no-repeat;
   background-size: 20px;
   background-position-y: center;
}

ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
}

li{
    margin-bottom: 20px;
}
