{/* ------------------------------ */}
{/* --- 프로젝트 메인 페이지 CSS ---- */}
{/* ------------------------------ */}

{/* 프로젝트 메인 - 컴포넌트 레이아웃 */}
.manage_container
{
    width:100%;
    height:100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    background-color: #191c22;
}

{/* 프로젝트 메인 - 프로젝트 리스트 컴포넌트 */}
.manage_list_container
{
    width:100%;
    height:100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    background-color: #191c22;
    overflow:auto;
}


{/* 프로젝트 메인 - 헤더 */}
.manage_header{
    grid-row: 1 / 2;
    width:100%;
    height:auto;
    position:sticky;
    margin-bottom:20px;
}

{/* 프로젝트 생성 - 현재 생성 및 수정 경로 */}
.path{
    height: 30px;
    width:100%;
    color: white;
    position:sticky;
}

.title
{
    width:100%;
    background-color: #191c22;

    display: grid;
    grid-template-columns: auto 200px;
}

.title_left
{
    grid-column: 1 / 2;
    color: white;
    font-size: 35px;
    font-weight: 700;
    background-color: #191c22;
}

{/* 데이터 생성 페이지 이름 입력 input */}
.title_left .input_field
{
  width: 40%;
  height: 50px;
  border-bottom: 1px solid white;
  display: flex;

  justify-content: space-between;
  align-items: center;

  /* 200220211 jpchoi - [ min-width : 560 -> 1170 ]*/
  min-width: 400px;
}


.title_left input
{
  font-size: 35px;
  font-weight: 700;
  border: none;
  outline: none;
  width: 100%;
  color:white;
  background: url("../images/icons/icon_3x/edit@3x.png") no-repeat;
  background-size: 30px;
  background-position-y: center;
  background-position-x: right;
}

.title_right
{
    grid-column: 2 / 2;
    background-color: #191c22;
}

{/* 프로젝트 메인 - 프로젝트 생성 버튼 */}
#create_button{
    color: #c2c8d3;
    width: 100%;
    height: 50px;
    border: 1px solid #c2c8d3;
    border-radius: 3px;
    cursor: pointer;
    font-size: 15px;
    background-color:#191c22;
    font-weight: 700;
    margin-top:0px;
    padding : 0px;
    align-items: center;
    justify-content: center;
    display:flex;
}

{/* 프로젝트 메인 - 헤더 */}
.project_manage_content{
    grid-row: 2 / 2;
    width: 100%;
    overflow:auto;
    padding: 0px 0px 0px 0px;
}

.project_manage_list
{
    width: 100%;
    height: auto;

        display: flex;
    flex-wrap: wrap;

    gap: 25px;
    overflow:auto;
}

{/* 프로젝트 메인 - 프로젝트 아이템 */}
.item_box
{
    padding: 12px;
    width: 288px;
    height: 270px;
    border: 2px solid transparent;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    position: relative;
    background: #242933;
}

{/* 프로젝트 메인 - 프로젝트 아이템 (마우스 오버 동작시) */}
.item_box:hover
{
    border-color: #448fff;
}

{/* 프로젝트 메인 - 프로젝트 아이템 제목 (현재 사용 안함) */}
.item_title {
    font-size: 15px;
}

{/* 프로젝트 메인 - 프로젝트 아이템 내용 */}
.item_content {
    height: 55%;
    font-size: 20px;
    color: white;
    width: 95%;
    word-break:break-all;
}

{/* 프로젝트 메인 - 프로젝트 아이템 바닥 (프로그래스 바) */}
.item_footer {
    width: 100%;
    height: 8%;
}

{/* ------------------------------ */}
{/* --- 프로젝트 생성 페이지 CSS ---- */}
{/* ------------------------------ */}

.manage_bottom
{
    height:100%;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.09fr 0.9fr 0.01fr;
}

.manage_bottom_menu
{
    grid-row: 1 / 2;
}

.manage_bottom_component
{
    grid-row: 2 / 3;

    width:100%;
    height:100%;

    flex-wrap: wrap;
    width: auto;
    overflow:auto;
}

.manage_bottom_footer
{
    grid-row: 3 / 4;
    width:100%;
}




#prevButton
{
    margin-top: 10px;
    background-color: #242933;
    font-size: 17px;
    font-weight: 50;
    width: 100%;
    height: 50px;
    border: none;
    color: white;
    border-radius: 3px;
}

#nextButton
{
    margin-top: 10px;
    background-color: #4a80ff;
    font-size: 17px;
    font-weight: 50;
    width: 100%;
    height: 50px;
    border: none;
    color: white;
    border-radius: 3px;
}


{/* ------------------------------ */}
{/* --- 데이터 셋 관리 페이지 CSS ---- */}
{/* ------------------------------ */}

.data_manage_content
{
    overflow:auto;
}

.data-mgmt-list {
  margin-bottom: 10px;
  height: 150px;

  border: none;
  border-radius: 5px;

  display: flex;

  cursor: pointer;
  background: #242933;
}

.data-mgmt-list:hover {
  border: 1px solid #448fff;
}

.data-mgmt-list .data-mgmt-item-image {
  width: 110px;
  height: 110px;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  margin-left: 20px;
  background-color: #3a414f;
}

.data-mgmt-list .data-mgmt-item-content {
  padding: 20px;
  width: calc(100% - 120px);

  position: relative;
}

.data-mgmt-item-content hr {
  border: 1px solid #364053;
  transform: scaleY(0.5);
}

