{/* 헤더 아이콘 전체 레이아웃 */}
.icon-bar {
    width: 100%; /* Full-width */
    overflow: auto; /* Overflow due to float */
    float: right;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    position: relative;
}

{/* 헤더 아이콘 - 사용자 정보 레이아웃 */}
.icon_user{
    float: right;
    text-align: center;
    width: auto;
    height:40px;
    color: white;
    font-size: 15px;
    margin-right:30px;
}

{/* 헤더 아이콘 - 사용자 정보 */}
#user_id{
    color: #8d97a9;
    height:100%;
    display:flex;
    align-items:center;
}

{/* 헤더 아이콘 - 알림 및 설정 레이아웃 */}
.icon_setting{
    float: right;
    text-align: center;
    width: auto;
    height:40px;
    color: white;
    font-size: 15px;
    margin-right:20px;
}

.icon_notify{
    float: right;
    text-align: center;
    width: auto;
    height:40px;
    color: white;
    font-size: 15px;
    margin-right:20px;
}

{/* 헤더 아이콘 - 알림 */}
#icon_notify_img
{
    color: #8d97a9;
    height:100%;
    width: 40px;
    display:flex;
    align-items:center;

    background: url("../images/icons/icon_3x/notify@3x.png") no-repeat;
    background-size: 40px;
    background-position-y: center;
    background-position-x: center;
}

{/* 헤더 아이콘 - 알림 마우스 오버 */}
.icon_notify:hover {
    background: url("../images/icons/icon_3x/notify_on@3x.png") no-repeat;
    background-size: 40px;
    background-position-y: center;
    background-position-x: center;
}

{/* 헤더 아이콘 - 설정 */}
#icon_setting_img
{
    color: #8d97a9;
    height:100%;
    width: 40px;
    display:flex;

    align-items:center;
    background: url("../images/icons/icon_3x/setting@3x.png") no-repeat;
    background-size: 40px;
    background-position-y: center;
    background-position-x: center;
}

{/* 헤더 아이콘 - 설정 마우스 오버 */}
.icon_setting:hover {
    background: url("../images/icons/icon_3x/setting_on@3x.png") no-repeat;
    background-size: 40px;
    background-position-y: center;
    background-position-x: center;
}
